.chat_add {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 10% 0;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;

    .chat_add_header {
        font-size: 2em;
        margin-bottom: 2rem;

    }

    .chat_add_body {
        width: 70%;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        .friend_item {
            width: 75px;
            height: 75px;
            flex-shrink: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
            
            .friend_item_avatar{
                width: 50px;
                height: 50px;
                flex-shrink: 0;

            }

            .friend_item_name{
                max-width: 75px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
}