.we_chat_left {
    display: flex;
    flex-direction: column;
    align-items: center;

    .avatar,
    .chat,
    .friend,
    .favorites,
    .me {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
    }

    .avatar {
        width: 2rem;
        height: 2rem;
        border-radius: 2px;
        background-image: url("data:image/jpeg;base64,");
        margin-top: 1rem;
    }

    .chat {
        width: 1.5rem;
        height: 1.35rem;
        margin-top: 1.5rem;
        background-image: url('../assets/img/chat1.png');

        &.is_slected {
            background-image: url('../assets/img/chat2.png');
        }
    }

    .friend {
        width: 1.6rem;
        height: 1.5rem;
        margin-top: 1.5rem;
        background-image: url('../assets/img/friend1.png');

        &.is_slected {
            background-image: url('../assets/img/friend2.png');
        }

    }

    .favorites {
        width: 1.6rem;
        height: 1.5rem;
        margin-top: 1.5rem;
        background-image: url('../assets/img/favorites1.png');

        &.is_slected {
            background-image: url('../assets/img/favorites2.png');
        }

    }

    .me {
        width: 1.5rem;
        height: 1.45rem;
        margin-top: 1.5rem;
        background-image: url('../assets/img/me1.png');

        &.is_slected {
            background-image: url('../assets/img/me2.png');
        }

    }
}