.we_chat {
    min-width: 40rem;
    width: 100%;
    height: 100%;
    overflow: auto hidden;
    background-color: black;
    display: flex;

    .we_chat_left {
        flex-shrink: 0;
        width: 3rem;
        height: 100%;
        background-color: #1E1E1E;
        overflow: hidden;
    }

    .we_chat_center {
        flex-shrink: 0;
        width: 14rem;
        height: 100%;
        background-color: #F7F7F7;
        overflow: hidden;
        border-right: 1px solid #D6D6D6;


        .context-menu-option {
            &:hover {
                background-color: #f0f0f0;
            }
        }

    }

    .we_chat_right {
        flex-shrink: 1;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: #F5F5F5;
    }




    ::-webkit-scrollbar {
        width: 3px;
        /* 设置滚动条的宽度 */
    }

    ::-webkit-scrollbar-track {
        background: #F5F5F5;
        /* 设置滚动条轨道的背景色 */
    }

    ::-webkit-scrollbar-thumb {
        background: #888;
        /* 设置滚动条滑块的背景色 */
        border-radius: 3px;
        /* 设置滚动条滑块的圆角 */
    }


    ::-webkit-scrollbar-thumb:hover {
        background: #555;
        /* 设置滚动条滑块在悬停时的背景色 */
    }



    .msg-block {
        -webkit-user-select: text;
        user-select: text;

        p {
            margin: 0;
        }

        ol {
            padding-inline-start: 25px;
        }

        ul {
            padding-inline-start: 25px;
        }

        img {
            width: 100%;
            max-width: 40rem;
        }
    }
}