.friend_add {
    width: 100%;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .friend_add_header {
        font-size: 2em;
        margin-bottom: 2rem;

    }

    .friend_add_body {
        height: 50%;
        width: 70%;

        table {
            width: 100%;

            tr {
                width: 100%;

                // display: flex;
                td {
                    &:nth-child(1) {
                        width: 20%;
                    }

                    &:nth-child(2) {
                        width: 80%;

                        textarea {
                            max-width: 20rem;
                            min-width: 14rem;
                            min-height: 5rem;
                        }
                    }


                }

            }

            .avatar {
                width: 3rem !important;
                height: 3rem;
                border-radius: 3px;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center;
                cursor: pointer;
                background-color: #E2E2E2;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 3em;
                font-weight: 200;
                user-select: none;

                span {
                    width: fit-content;
                    height: fit-content;
                }
            }
        }
    }
}