.chat_list {
    width: 100%;
    min-height: 100%;
    overflow: hidden;

    .chat_list_header {
        box-sizing: border-box;
        width: 100%;
        height: 3.75rem;
        padding: 1.5rem 0.75rem 0.75rem;
        display: flex;
        justify-content: space-between;

        .chat_list_input {
            box-sizing: border-box;
            outline: none;
            border: 0;
            background-color: #E2E2E2;
            border-radius: 4px;
            // padding-left: 1.5rem;
            padding-left: 0.5rem;
            width: 10.5rem;
        }

        .chat_list_add {
            width: 1.5rem;
            height: 1.5rem;
            line-height: 1.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.2em;
            border-radius: 4px;
            background-color: #E2E2E2;
            cursor: pointer;
            user-select: none;
            color: #585858;
            font-weight: 200;
            &:hover{
                background-color: #D1D1D1;
            }
        }
    }
    
    

}